//Reminder.js

/*
 * Reminder
 *
 * List all the features :
 *  - save a repeating ( OR a non-repeating ) reminder to the server.
 */
import React, { Component } from 'react';
import { compareDates } from '../Calendar/CalendarUtils';
import moment from 'moment';


const FILE_NAME = "Reminder.js";

class Reminder extends Component {

    constructor(props) {
        super(props);
        this.state = {
            isLoaded: false,
            reminderEnds: true,
            reminderRepeats: false
        };
    }

    componentDidMount() {
        document.title = "Satanlabs - Submit a new reminder request";
        const todayDateStr = moment().format("YYYY-MM-DD");
        console.log(todayDateStr);
    }

    submitForm = (event) => {
        event.preventDefault();

        let data = {
            "title"     : event.target.newReminderTitle.value,
            "startDate" : event.target.newReminderStartDate.value,
            "startTime" : event.target.newReminderStartTime.value,
            "text"      : event.target.newReminderText.value,
            "notes"     : event.target.newReminderNote.value,

            "repeats"   : false,
            "repetitionType" : null,
            "repetitionFrequency" : null,

            "ends"       : false,
            "endsOnDate" : null
        };

        if(this.state.reminderRepeats) {
            data.repeats = true;
            data.repetitionType = event.target.newReminderRepetitionType.value;
            data.repetitionFrequency = event.target.newReminderRepetitionFrequency.value;

            if(this.state.reminderEnds) {
                //:todo - add check that the `endsOndate` is gr8er than the `startDate`
                const endsOnDate = event.target.newReminderEndsOnDate.value;

                if(compareDates(data.startDate, endsOnDate)  !== 1) {
                    alert("ends-on date should be greater than start-date.");
                    return;
                }
                
                data.ends = true;
                data.endsOnDate = endsOnDate;
            }
        }

        console.log(JSON.stringify(data));
    }

    toggleEndsOnSection = (event) => {
        this.setState({
            reminderEnds: event.target.checked
        });
    }

    toggleReminderRepeatsSection = (event) => {
        this.setState({
            reminderRepeats: event.target.checked
        });
    }

    render() {
        console.log(FILE_NAME + " render() : this.state :", this.state);

        return (
            <div>
                <form onSubmit={this.submitForm}>

                    <b>Title : </b>
                    &nbsp;&nbsp;&nbsp;&nbsp;
                    <input type="text" name="newReminderTitle" id="newReminderTitle" required={true} />
                    <br /><br />

                    {
                      (this.state.reminderRepeats) ? 
                      <b>Start Reminding me on :</b>  : <b>Remind me on :</b>
                    }
                    &nbsp;&nbsp;&nbsp;&nbsp;
                    <br /><br />
                    DATE : <input type="date" name="newReminderStartDate" id="newReminderStartDate" required={true} />
                    &nbsp;&nbsp;&nbsp;&nbsp;
                    TIME : <input type="time" name="newReminderStartTime" id="newReminderStartTime" required={true} defaultValue="19:00" />
                    <br /><br />

                    <b>Repeats ?</b>
                    &nbsp;&nbsp;&nbsp;&nbsp;
                    <input type="checkbox" id="reminderRepeats" name="reminderRepeats" style={{ width: "20px", height: "20px" }} defaultChecked={this.state.reminderRepeats} onChange={this.toggleReminderRepeatsSection} />
                    <br /><br />

                    {
                        (this.state.reminderRepeats == true) &&
                        (
                            <div>
                                <b>REMINDER TYPE : </b>
                                Repeat after every 
                                &nbsp;&nbsp;&nbsp;&nbsp; 
                                <input type="number" name="newReminderRepetitionFrequency" id="newReminderRepetitionFrequency" defaultValue={1} required={true} />
                                &nbsp;&nbsp;&nbsp;&nbsp;
                                <select required={true} name="newReminderRepetitionType" id="newReminderRepetitionType">
                                    <option value="">select a value</option>
                                    <option value="min">Minute(s)</option>
                                    <option value="hour">Hour(s)</option>
                                    <option value="day">Day(s)</option>
                                    <option value="week">Week(s)</option>
                                    <option value="month">Month(s)</option>
                                    <option value="year">Year(s)</option>
                                </select>
                                <br /><br />

                                <b>Ends ?</b>
                                &nbsp;&nbsp;&nbsp;&nbsp;
                                <input type="checkbox" id="reminderEnds" name="reminderEnds" style={{ width: "20px", height: "20px" }} defaultChecked={this.state.reminderEnds} onChange={this.toggleEndsOnSection} />
                                <br /><br />

                                {
                                    (this.state.reminderEnds) &&
                                    (
                                        <div>
                                            <b>Ends on :</b>
                                            &nbsp;&nbsp;&nbsp;&nbsp;
                                            DATE : <input type="date" name="newReminderEndsOnDate" id="newReminderEndsOnDate" required={true} />
                                            <br /><br />
                                        </div>
                                    )
                                }
                            </div>
                        )
                    }

                    
                    <b>ACTUAL REMINDER TEXT : ( CAUTION : THIS WILL BE ACTUALLY ANNOUNCED, via the speaker ... ) </b>
                    <br />
                    <textarea rows="5" cols="50" id="newReminderText" style={{ backgroundColor: "#ffdbde" }} placeholder="Actual reminder message..." required={true} />
                    <br /><br />

                    <b>REMINDER NOTES :</b>
                    <br />
                    <textarea rows="3" cols="50" id="newReminderNote" style={{ backgroundColor: "#ffdbde" }} placeholder="Note for the reminder..." required={true} />
                    <br /><br />

                    <input type="submit" value="Add new reminder" />
                    <input type="reset" value="Reset form" />
                </form>
            </div>
        );
    }
}

export default Reminder;