/*
for : January 2020
input -> 0 , 2020 
*/
const getMonthInfo = (month, year) => {
    if (month >= 0 && month <= 11 && year > 0) {
        let monthInfo = allMonths[month];

        if (month == 1 && year % 4 == 0) {
            monthInfo.noOfDays = 29;
        }

        return monthInfo;
    }
    else {
        throw Error();
    }
}


const getWeekdayInfo = (someDay) => {
    return allWeekdays[someDay.getDay()];
}

/*
accepts date-string in format : `YYYY-MM-DD`

returns :
 -1 if `date1` is bigger,
 +1 if `date2` is bigger,
  0 if both are equal.
*/
const compareDates = (date1String, date2String) => {
    const d1 = new Date(date1String);
    const d2 = new Date(date2String);
    const diff = d1.getTime() - d2.getTime();

    if(diff > 0)
        return -1;
    else if(diff < 0)
        return +1;
    else
        return 0;
}

export {
    allWeekdays,
    allMonths,
    getMonthInfo,
    getWeekdayInfo,
    compareDates
};






